.bg-color {
  background-color: #efa6b7;
}

.nav-list,
.header__button {
  display: none;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
}
.logo__icon {
  fill: $accentColor;
}
.mobile-menu__burger-icon {
  stroke: $accentColor;
}
.mobile-menu__open-btn:hover .mobile-menu__burger-icon,
.mobile-menu__open-btn:focus .mobile-menu__burger-icon {
  stroke: $mainColor;
}
.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100%;
  background-color: $mobileMenuBgColor;

  transform: translateX(100%);
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);

  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  /* overflow-y: scroll;
        z-index: 1; */
}
.mobile-menu.is-open {
  transform: translateX(0);

  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}
.mobile-menu__container {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 60px 20px;
}
.mobile-menu__close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  border: none;
  background-color: transparent;
  padding: 0;
  line-height: 0;
}
.mobile-menu__close-icon {
  stroke: $mainColor;
}
.mobile-menu__close-btn:hover .mobile-menu__close-icon,
.mobile-menu__close-btn:focus .mobile-menu__close-icon {
  stroke: $accentColor;
}
.mobile-menu__button {
  margin-bottom: 32px;
  min-width: 186px;
  padding-left: 48px;
  padding-right: 48px;
}
.mobile-menu__button:hover,
.mobile-menu__button:focus {
  background-color: $buttonHoverColor;
}
.mobile-menu__nav-item:not(:last-child) {
  margin-bottom: 23px;
}
.mobile-menu__nav-item {
  position: relative;
}
.mobile-menu__nav-item::before {
  content: '';
  position: absolute;
  display: block;
  top: -10px;
  left: -20px;
  height: 1px;
  width: 120%;
  background-color: #e18298;
}
.mobile-menu__nav-item:last-child::after {
  content: '';
  position: absolute;
  display: block;
  top: 30px;
  left: -20px;
  height: 1px;
  width: 120%;
  background-color: #e18298;
}

.mobile-menu__nav-link {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;
  text-decoration: none;
  color: $mainColor;
}
.mobile-menu__nav-link:hover,
.mobile-menu__nav-link:focus {
  color: $accentColor;
}

@media screen and (min-width: 768px) {
  .header__button {
    display: inline-block;
    min-width: 160px;
    padding-left: 37px;
    padding-right: 37px;
  }
  .header__button:hover,
  .header__button:focus {
    background-color: $buttonHoverColor;
  }
  .header__container {
    padding-top: 32px;
  }
  .mobile-menu__open-btn {
    margin-left: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .mobile-menu__open-btn,
  .mobile-menu {
    display: none;
  }
  .header__container {
    padding-top: 40px;
  }
  .nav-list {
    margin-left: 128px;
    display: flex;
  }
  .header__navigation {
    display: flex;
    align-items: center;
  }
  .nav-list__item:not(:last-child) {
    margin-right: 35px;
  }
  .nav-list__link {
    text-decoration: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    color: $mainColor;
  }
  .nav-list__link:hover,
  .nav-list__link:focus {
    color: $accentColor;
  }
}
